$background: #FFFFFF;
$dark-shade: #1F2041;
$dark25: #1F204140;
$green: #BC9CFF;
$purple: #6FCF97; 

body {
    box-sizing: border-box;
    padding: 22px;
    // padding: 137px 142px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: $dark-shade;
    background: $background;
}

h1 {
    font-family: "Quicksand";
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
}

h2 {
    font-family: "Quicksand";
    font-weight: bold;
    font-size: 19px;
    line-height: 24px;
}

h3 {
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    margin: 4px 0px;
}

.cnt-column {
    padding-left: 120px;
    padding-top: 60px;
    float: left;
    width: 50%;
}

.cnt-clmn-r {
    padding-left: 320px;
    padding-top: 47px;
    float: left;
    width: 50%;
}

.color-flex {
    display: flex;
    margin-bottom: 20px;
}

.color-rect {
    width: 70px;
    height: 70px;
    left: 150px;
    top: 145px;
    border-radius: 6px;
    margin-right: 40px; 
}

.color-description {
    display: flex;
    flex-direction: column;
}

.hint {
    text-align: right;
    margin-right: 40px;
    font-family: "Montserrat";
    font-weight: normal;
    color: #1F204140;
}

.text-input__input {
    background: #FFFFFF;
    border: 1px solid rgba(31, 32, 65, 0.25);
    box-sizing: border-box;
    border-radius: 4px;
    width: 320px;
    height: 44px;
    ::placeholder {
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        color: $dark25
    }
}

::placeholder {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    color: $dark25
}